/* tslint:disable */
import React, { Component } from 'react';
import './style/karel.css'
import Button from 'react-bootstrap/Button';
import Swal from 'sweetalert2'
import KarelNorth from './images/classic/karelNorth.png'
import KarelSouth from './images/classic/karelSouth.png'
import KarelEast from './images/classic/karelEast.png'
import KarelWest from './images/classic/karelWest.png'
import Beeper from './images/beeper.png'

const KAREL_IMG_PCT = 0.8
const BEEPER_IMG_PCT = 0.5
const CROSS_PCT = 0.1

class KarelWorld extends Component {

  static stateEquals(a, b) {
    if(a.karelRow != b.karelRow) return false
    if(a.karelCol != b.karelCol) return false
    if(a.dir != b.dir) return false

    // check stones
    let nRowsA = a.stones.length
    let nColsA = a.stones[0].length
    let nRowsB = b.stones.length
    let nColsB = b.stones[0].length
    if(nRowsA != nRowsB) return false
    if(nColsA != nColsB) return false
    for (var r = 0; r < nRowsA; r++) {
      for (var c = 0; c < nColsA; c++) {
        if(a.stones[r][c] != b.stones[r][c]) {
          return false
        }
      }
    }

    // WARNING: doesn't check walls
    return true
  }

  resize() {
    this.updateDimensions()
  }

  updateDimensions() {
    let parentWidth = this.refs.ideCanvas.parentNode.clientWidth
    let aspectRatio = this.initialWorld.nRows / this.initialWorld.nCols
    let worldHeight = aspectRatio * parentWidth
    this.setState({ width: parentWidth, height: worldHeight });
  };

  componentWillMount() {
    this.initialWorld = this.props
    this.setStateFromData(this.props, () => {})
  }

  componentDidMount() {
    if('width' in this.props) {
      this.setState({
        'width':this.props.width,
        'height':this.props.height
      })
    } else {
      this.updateDimensions()
    }
  }

  // used to change a world
  setNewWorld(worldData) {
    this.initialWorld = worldData
    this.setStateFromData(this.initialWorld, () => this.updateDimensions())
  }


  setStateFromData(worldData, callbackFn) {
    var karelRow = worldData.nRows - 1
    if('karelRow' in worldData){
      karelRow = worldData.karelRow
    }

    var karelCol = 0
    if('karelCol' in worldData) {
      karelCol = worldData.karelCol
    }

    var karelDir = 'East'
    if('karelDir' in worldData) {
      karelDir = worldData.karelDir
    }

    var walls = []
    if('walls' in worldData) {
      walls = worldData.walls
    }

    const stones = this.initStones(worldData);

    this.setState({
      nRows:worldData.nRows,
      nCols:worldData.nCols,
      karelRow:karelRow,
      karelCol:karelCol,
      dir:karelDir,
      stones:stones,
      walls:walls,
      colors:[],
    }, () => {
      this.setState({
        descriptions:[this.renderWorldDescription({
          karelRow: karelRow,
          karelCol: karelCol,
          karelDir: karelDir,
          stones:   stones,

          walls:    worldData.walls,
          nRows:    worldData.nRows,
          nCols:    worldData.nCols,
        })]
      }, callbackFn)
    })
  }

  getWorldState() {
    return this.state
  }

  reset(callbackFn) {
    this.setStateFromData(this.initialWorld, callbackFn)
  }

  initStones(worldData) {
    var stones = []
    // make a grid of zeros
    for(var r = 0; r < worldData.nRows; r++) {
      var row = []
      for(var c = 0; c < worldData.nCols; c++) {
        row.push(0)
      }
      stones.push(row)
    }
    // look for initialized stones
    if('stones' in worldData) {
      for (var i = 0; i < worldData.stones.length; i++) {
        var stone = worldData.stones[i]
        stones[stone.r][stone.c] = stone.n
      }
    }
    return stones
  }

  setStepCallback(callbackFn) {
    this.onStepFinished = callbackFn;
  }

  setErrorCallback(errorCallbackFn) {
    this.onError = errorCallbackFn
  }

 /***********************************************
  *     Commands                                *
  ***********************************************/

  move() {
    let oldRow = this.state.karelRow;
    let oldCol = this.state.karelCol;
    var newRow = oldRow;
    var newCol = oldCol;
    switch(this.state.dir) {
      case 'East': newCol++; break;
      case 'West': newCol--; break;
      case 'North': newRow = newRow - 1; break;
      case 'South': newRow = newRow + 1; break;
      default: console.console.error("invalid this.dir: " + this.dir); break;
    }

    const beeperMsg = this.currBeeperDescription(this.state.stones[newRow][newCol])
    const descr = `Karel moved one step forward and is now at row ${newRow} and column ${newCol}, facing ${this.state.dir}. ` + beeperMsg;
    if(this.isMoveValid(oldRow, oldCol, newRow, newCol)) {
      this.setState({
        karelRow:newRow,
        karelCol:newCol,
        descriptions: [...this.state.descriptions, descr]
      }, this.onStepFinished)
    } else {
      this.error('Front is blocked');
    }
  }

  pick_beeper() {
    let row = this.state.karelRow;
    let col = this.state.karelCol
    if (this.beepers_present()) {
      let stones = this.state.stones
      stones[row][col]--
      const beeperMsg = this.currBeeperDescription(stones[row][col], true)
      this.setState({
        stones:stones,
        descriptions: [...this.state.descriptions, 'Karel picked up a beeper. ' + beeperMsg]
      }, this.onStepFinished);
    } else {
      this.error('No beepers present');
    }
  }

  put_beeper() {
    let row = this.state.karelRow;
    let col = this.state.karelCol
    let stones = this.state.stones
    stones[row][col]++
    const beeperMsg = this.currBeeperDescription(stones[row][col], true)
    this.setState({
      stones:stones,
      descriptions: [...this.state.descriptions, 'Karel put a beeper. ' + beeperMsg]
    }, this.onStepFinished);
  }

  turn_left() {
    var newD = this.state.dir;
    switch(this.state.dir) {
      case 'East':  newD = 'North'; break;
      case 'West':  newD = 'South'; break;
      case 'North': newD = 'West'; break;
      case 'South': newD = 'East'; break;
      default: alert("invalid this.dir: " + this.dir); break;
    }
    this.setState({
      dir:newD,
      descriptions: [...this.state.descriptions, `Karel turned left and is now facing ${newD}`]
    }, this.onStepFinished)
  }

  front_is_clear() {
    let oldRow = this.state.karelRow;
    let oldCol = this.state.karelCol;
    var newRow = oldRow;
    var newCol = oldCol;
    switch(this.state.dir) {
      case 'East': newCol++; break;
      case 'West': newCol--; break;
      case 'North': newRow = newRow - 1; break;
      case 'South': newRow = newRow + 1; break;
      default: console.console.error("invalid this.dir: " + this.dir); break;
    }
    return this.isMoveValid(oldRow, oldCol, newRow, newCol)
  }

  beepers_present() {
    let r = this.state.karelRow
    let c = this.state.karelCol
    return this.state.stones[r][c] > 0
  }

  no_beepers_present() {
    return !this.beepers_present()
  }

  beepers_in_bag(){
    return true
  }
  left_is_clear() {
    console.log('HELLO WORLD')
    // strategy here is to try and move karel to the left and then see if 
    // is move valid says its ok!
    let oldRow = this.state.karelRow;
    let oldCol = this.state.karelCol;
    var newRow = oldRow;
    var newCol = oldCol;
    switch(this.state.dir) {
      case 'East': newRow--; break;
      case 'West': newRow++; break;
      case 'North': newCol--; break;
      case 'South': newCol++; break;
      default: console.console.error("invalid this.dir: " + this.dir); break;
    }
    console.log('left is clear', oldRow, oldCol, newRow, newCol)
    return this.isMoveValid(oldRow, oldCol, newRow, newCol)
  }
  right_is_clear() {
    let oldRow = this.state.karelRow;
    let oldCol = this.state.karelCol;
    var newRow = oldRow;
    var newCol = oldCol;
    switch(this.state.dir) {
      case 'East': newRow++; break;
      case 'West': newRow--; break;
      case 'North': newCol++; break;
      case 'South': newCol--; break;
      default: console.console.error("invalid this.dir: " + this.dir); break;
    }
    return this.isMoveValid(oldRow, oldCol, newRow, newCol)
  }
  facing_north() {
    return this.state.dir == 'North'
  }
  facing_south() {
    return this.state.dir == 'South'
  }
  facing_east() {
    return this.state.dir == 'East'
  }
  facing_west() {
    return this.state.dir == 'West'
  }
  front_is_blocked() {
    return !this.front_is_clear()
  }
  no_beepers_in_bag() {
    return !this.beepers_in_bag()
  }
  left_is_blocked() {
    return !this.left_is_clear()
  }
  right_is_blocked() {
    return !this.right_is_clear()
  }
  not_facing_north() {
    return !this.facing_north()
  }
  not_facing_south() {
    return !this.facing_south()
  }
  not_facing_east() {
    return !this.facing_east()
  }
  not_facing_west() {
    return !this.facing_west()
  }

  random() {
    return Math.random() < 0.5
  }

  paint_corner(color) {
    this.put_beeper()
  }

 /***********************************************
  *     Model                                   *
  ***********************************************/

  isMoveValid(startR, startC, endR, endC) {
    if(endC < 0 || endC >= this.state.nCols) return false;
    if(endR < 0 || endR >= this.state.nRows) return false;

    var dRow = Math.abs(endR - startR);
    var dCol = Math.abs(endC - startC);

    // check for walls
    let isEast = startC + 1 === endC
    let isWest = startC - 1 === endC
    let isNorth = startR - 1 === endR
    let isSouth = startR + 1 === endR

    // walls are only north and east
    if(isEast && this.hasEastWall(startR, startC)) return false
    if(isWest && this.hasEastWall(endR, endC)) return false
    if(isNorth && this.hasNorthWall(startR, startC)) return false
    if(isSouth && this.hasNorthWall(endR, endC)) return false

    // can only move 1 manhattan distance
    if (dRow + dCol != 1) return false;

    return true
  }

  hasEastWall(r, c) {
    return this.hasWall(r, c, 'East')
  }

  hasNorthWall(r, c) {
    return this.hasWall(r, c, 'North')
  }

  // dir can only be east or north...
  hasWall(r, c, dir) {
    for(let wallIndex in this.state.walls) {
      let wall = this.state.walls[wallIndex]
      if(wall.r == r && wall.c == c && wall.d == dir) {
        return true
      }
    }
    return false
  }

 /***********************************************
  *     Render                                *
  ***********************************************/

  error(message) {
    Swal.fire({
      title: message,
      icon: 'error',
      toast:true,
    })
    this.onError()
  }

  getCornerSize() {
    if(!this.refs.ideCanvas) {
      return 0
    }
    return this.state.width / this.state.nCols
  }

  getWorldLeft() {
    return 0
  }

  getWorldTop() {
    return 0
  }

  getCornerX(row, col) {
    return this.getWorldLeft() + col * this.getCornerSize();
  }

  getCornerY(row, col) {
    return this.getWorldTop() + row * this.getCornerSize();
  }

  getKarelImg() {
    switch(this.state.dir) {
      case 'North': return KarelNorth
      case 'South': return KarelSouth
      case 'East': return KarelEast
      case 'West': return KarelWest
      default: return KarelNorth
    }
  }

  renderInnerWalls() {
    var walls = []
    if(!('walls' in this.state || !this.state.walls)) return <div />
    for (var i = 0; i < this.state.walls.length; i++) {
      let wall = this.state.walls[i]

      if(wall.d == 'North') {
        let x = this.getCornerX(wall.r, wall.c)
        let y = this.getCornerY(wall.r, wall.c)
        walls.push(<div
          className="wall"
          style={{
            marginLeft:x,
            marginTop:y,
            width:this.getCornerSize(),
            height:2,
          }}
          key={i}
        ></div>)
      } else if(wall.d == 'East') {
        let x = this.getCornerX(wall.r, wall.c+1)
        let y = this.getCornerY(wall.r, wall.c)
        walls.push(<div
          className="wall"
          style={{
            marginLeft:x,
            marginTop:y,
            width:2,
            height:this.getCornerSize(),
          }}
          key={i}
        ></div>)
      } else {
        console.error('Walls must be North or East')
      }
    }
    return (<div>{walls}</div>)
  }

  renderGrid() {
    var lines = []
    for (var r = 0; r < this.state.nRows; r++) {
      for(var c = 0; c < this.state.nCols; c++) {
        let cornerSize = this.getCornerSize()
        let x = this.getCornerX(r, c) + 0.5 * cornerSize
        let y = this.getCornerY(r, c) + 0.5 * cornerSize
        let crossSize = cornerSize * CROSS_PCT
        lines.push(<div
          className="cross"
          style={{
            marginLeft:x - crossSize/2,
            marginTop:y-1,
            width:crossSize,
            height:2
          }}
          key={r+','+c +'1'}
        ></div>)
        lines.push(<div
          className="cross"
          style={{
            marginLeft:x-1,
            marginTop:y - crossSize/2,
            width:2,
            height:crossSize
          }}
          key={r+','+c +'2'}
        ></div>)
      }
    }

    return (<div>{lines}</div>)
  }

  renderStones() {
    var stoneList = []
    for (var r = 0; r < this.state.nRows; r++) {
      for(var c = 0; c < this.state.nCols; c++) {
        let nStones = this.state.stones[r][c]
        if(nStones > 0) {
          stoneList.push({r:r,c:c,n:nStones})
        }
      }
    }
    return <div>
      {
        stoneList.map((stone, index) => {
          let size = BEEPER_IMG_PCT * this.getCornerSize()
          let offset = (this.getCornerSize() - size)/2
          let x = this.getCornerX(stone.r, stone.c) + offset
          let y = this.getCornerY(stone.r, stone.c) + offset
          let text = stone.n > 1 ? stone.n : ''
          return (
            <div className="stone" style={{
              width:size,height:size,
              marginLeft:x,marginTop:y
            }}>
              <div
                key={index}
                className ="stoneDiamond"
              />
              <span className="stoneText" style={{
                fontSize:size * 0.75,
                marginTop:-size * 1.05,
                width:size
              }}>{text}</span>
            </div>
          )
        })
      }
    </div>
  }

  renderKarel() {
    let row = this.state.karelRow
    let col = this.state.karelCol
    let cornerSize = this.getCornerSize()
    let size = KAREL_IMG_PCT * this.getCornerSize()
    let offset = (cornerSize - size)/2
    let x = this.getCornerX(row, col) + offset
    let y = this.getCornerY(row, col) + offset
    return (<img
      className="karel"
      style={{marginLeft:x,marginTop:y}}
      width={size}
      height={size}
      src={this.getKarelImg()}>
    </img>)
  }


  renderWorld() {
    return (
      <div
        className= "ideCanvas"
        ref="ideCanvas"
        style={{
          width:this.state.width,
          height:this.state.height
        }}
      >
        {this.renderInnerWalls()}
        {this.renderGrid()}
        {this.renderStones()}
        {this.renderKarel()}
      </div>
    )
  }

  getWorldState() {
    return {
      karelRow: this.state.karelRow,
      karelCol: this.state.karelCol,
      karelDir: this.state.dir,
      stones:   this.state.stones,

      walls:    this.state.walls,
      nRows:    this.state.nRows,
      nCols:    this.state.nCols,
    }
  }

  denseToSparseStones(stones) {
    const ret = []
    // make a grid of zeros
    for(var r = 0; r < this.state.nRows; r++) {
      for(var c = 0; c < this.state.nCols; c++) {
        if(stones[r][c] > 0) {
           ret.push({r: r, c: c, n: stones[r][c]})
        }
      }
    }
    return ret
  }

  currBeeperDescription(nBeepers, now=false) {
    const fmt = [
      (nBeepers === 1) ? 'is' : 'are',
      (nBeepers > 0) ? `${nBeepers}` : 'no',
      (nBeepers === 1) ? 'beeper' : 'beepers'
    ]
    const currMsg = now ? "now" : ""
    return `There ${fmt[0]} ${currMsg} ${fmt[1]} ${fmt[2]} here.`
  }

  getStonesDescription(denseStones) {
    const stones = this.denseToSparseStones(denseStones);
    const stoneMsg = stones.map(stone => {
      const beeper = stone.n === 1 ? "beeper" : "beepers";
      return <li>{`${stone.n} ${beeper} at row ${stone.r}, column ${stone.c}`}</li>
    })
    if(stones.length === 0) {
      return (
        <div>
          There are no beepers in the world.
        </div>
      )
    }
    return (
      <span>
        {`The beepers are placed as follows:`}
        { stoneMsg }
      </span>
    );

  }

  renderWorldDescription(optionalDescr=null) {
    var descr = optionalDescr;
    if(!descr) {
      descr = this.getWorldState()
    }

    const overviewMsg = `Karel is in a world that has ${descr.nRows} rows and ${descr.nCols} columns. Karel is standing on row ${descr.karelRow} and column ${descr.karelCol}, facing ${descr.karelDir}. `;
    const stonesMsg = this.getStonesDescription(descr.stones);

    return (
        <span>
          <span>{overviewMsg}</span>
          <span>{stonesMsg}</span>
          <span>(End of world description)</span>
        </span>
    );
  }

  renderDescriptions() {
    return (
      <div className="descriptionContainer">
        <h5> World description </h5>
        <div style={{marginBottom: '10px'}}>{this.state.descriptions[0]}</div>

        <h5> Karel Actions </h5>

        {this.state.descriptions.length == 1 ?
          'No actions taken by Karel. Press run button below to run your code!' :
          <ol>{this.state.descriptions.slice(1).map((descr, idx) => <li >{descr}</li>)}</ol>
        }

      </div>
    )
  }


  render() {
    return (
      <div>
        <div>
          {this.renderWorld()}
        </div>
        { this.props.makeAccessible ? this.renderDescriptions() : null}
      </div>
    )
  }
}

export default KarelWorld