/*

If files is empty, then it will just go log in students.
Otherwise, if files is a list of files to upload, they get submitted!
They will have a chance to confirm.
Students can submit as many times that they want!
*/
export class SubmissionWithData extends Component {

  renderCodePost() {
    return <div
        style={{
          flex:1
        }}
      >
       <CodePostEmbed
         token={this.props.token}
         auth_type="Firebase"
         route="/"
         source="Code in Place"
         assignment={4050}
         files={[
           { name: "file1.py", data: "print('hello world!')" },
           { name: "file2.py", data: "def karel:\n  move()\n  move()" },
         ]}
       />
      </div>
  }

  render() {

    return (
      <div className="d-flex flex-column" style={{height:'100vh'}}>
        <CourseNav userName={this.props.userName}/>
        {this.renderCodePost()}

      </div>
    )


  }
}